.team {
  margin: 120px 0 150px;
  &__title {
    font-family: AvenirNext;
    font-size: 66px;
    font-weight: 900;
    letter-spacing: -0.47px;
    text-align: center;
    color: #fff;
  }
  &Body {
    margin: 80px 0 0 0;
    display: flex;
    justify-content: space-between;
    &__item {
      flex: 0 0 245px;
      &Img {
        padding: 0 0 100% 0;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
        }
      }
      &Name {
        display: flex;
        justify-content: space-between;
        margin: 25px 0 0 0;
        font-family: AvenirNext;
        font-size: 20px;
        font-weight: bold;
        letter-spacing: -0.14px;
        color: #fff;
      }
      &Link {
        display: inline-block;
        width: 18px;
        height: 18px;
        svg {
          width: 100%;
          height: 100%;
        }
      }
      &Pos {
        margin: 10px 0 0 0;
        opacity: 0.4;
        font-family: AvenirNext;
        font-size: 14px;
        font-weight: 600;
        letter-spacing: -0.1px;
        color: #fff;
      }
    }
  }
}